import back from '@/components/back'
export default {
	components:{
		back,
	},
	data() {
		return {
			id:this.$route.query.id,//该病情资料对应的问诊关联id,
			info:null,
			imgList:[],
			imgThumbnail:[],
			type:this.$route.query.type,//该病情资料对应的问诊关联类型,
		}
	},
	created() {
		this.pullInfo()
	},
	methods: {
		pullInfo(){
			//获取该问诊订单的id
			let _url = "/ConsultAPI/Consult/GetConsultInfo"
			if(this.type && (this.type == 1 || this.type == 5)){
				_url = "/ConsultAPI/Consult/GetConsultInfo"
			}else if(this.type && (this.type == 2 || this.type == 6)){
				_url = "/ConsultAPI/Consult/GetInquiryInfo"
			}else if(this.type && this.type == 3){
				_url = "/Prescription/Prescription/GetPrescriptionApplyInfo"
			}else if(this.type && this.type == 15){
				_url = "/ConsultAPI/Consult/GetRemoteOutpatientInfo"
			}
			this.$post(_url,{
                Id:this.id
            }).then(o => {
				this.info = o
				if(this.info.ConsultImgs){
					this.info.ConsultImgs.forEach(v => {
						let thumb = v.PicturePath.split('/')
						thumb[thumb.length - 1] = `Thumbnail/${thumb[thumb.length-1]}`
						let thumbnail = thumb.join("/")
						this.imgThumbnail.push(thumbnail)
						this.imgList.push(v.PicturePath)
					})
				}
        	})
		},
		preview(k) {
			const data = {
				position: k,
				url: this.imgList
			}
			this.$bridge.callhandler('Preview', JSON.stringify(data))
		},
		
	},
	mounted: function() {
		
	},
}



